import Axios, {AxiosResponse} from 'axios'
import React, {useRef, useState} from 'react'
import {URL_BOOKING_STUDIO} from '../../../../../../setup/URL'
import {FormInputItem} from '../../../../../components/FormItem/FormItem'
import {SMTP} from '../SMTPSetting'
type Props = {
  id: number
  flag: any
}
const EditSMTP = React.memo(({id, flag}: Props) => {
  const [detail, setDetail] = useState<SMTP>({
    id: 0,
    Host: '',
    Service: '',
    Mail: '',
    Password: '',
    createdAt: '',
    updatedAt: '',
  })
  const [errors, setErrors] = useState({
    Host: false,
    Service: false,
    Mail: false,
    Password: false,
  })
  const typingTimeOutRef = useRef<any>(null)
  const getSMTPId = () => {
    let promise = Axios({
      url: `${URL_BOOKING_STUDIO}smtp/${id}`,
      method: 'GET',
    })
    promise.then((result: AxiosResponse<any>) => {
      console.log('Thanh cong')
      //Nếu gọi api thành công
      // => set lại state
      setDetail(result.data)
    })
    promise.catch((err) => {
      console.log('That bai')
      console.log(err.response.data)
    })
  }
  const lableName = Object.keys(detail)
  const error = Object.values(errors)
  const handleChangeValue = (name: string, value: any, index: number) => {
    setDetail({...detail, [name]: value})
    if (value === '' && name === lableName[index]) {
      setErrors({...errors, [name]: true})
    } else {
      setErrors({...errors, [name]: false})
    }
  }
  const SMTPdetail = () => {
    return Object.values(detail)
      .slice(1, 5)
      .map((itm, index) => (
        <FormInputItem
          lableName={lableName[index + 1]}
          name={lableName[index + 1]}
          value={itm}
          key={index}
          errors={error[index]}
          onChange={(e: any) => {
            let {name, value} = e.target
            handleChangeValue(name, value, index + 1)
          }}
          styleLable={{
            transform: `translate(${0}px, ${-11}px)`,
            color: '#1EC0FF',
            left: '12px',
            fontWeight: '700',
          }}
          styleInput={{border: '1px solid #1EC0FF', color: '#616161', minHeight: '46px'}}
        />
      ))
  }
  const updateSMTP = async () => {
    if (!errors.Host && !errors.Service && !errors.Password && !errors.Mail) {
      let promise = Axios({
        url: `${URL_BOOKING_STUDIO}smtp/${id}`,
        method: 'PATCH',
        data: {
          Host: detail.Host,
          Service: detail.Service,
          Mail: detail.Mail,
          Password: detail.Password,
        },
      })
      promise.then((result: AxiosResponse<any>) => {
        if (typingTimeOutRef.current) {
          clearTimeout(typingTimeOutRef.current)
        }
        typingTimeOutRef.current = setTimeout(() => {
          flag(true)
        }, 500)
      })
      promise.catch((err) => {
        console.log('That bai')
        console.log(err)
      })
    }
  }
  return (
    <>
      <button
        type='button'
        className='edit-button-SMTP '
        data-bs-toggle='modal'
        data-bs-target={`#editModal${id}`}
        onClick={() => {
          getSMTPId()
        }}
      >
        <i className='far fa-edit text-dark'></i>
      </button>
      <div
        className='modal fade'
        id={`editModal${id}`}
        tabIndex={-1}
        aria-labelledby='exampleModalLabel'
        aria-hidden='true'
      >
        <div className='modal-dialog '>
          <div className='modal-content w-500px mx-auto  ' style={{top: '200px'}}>
            <div className='modal-body'>
              <div className='d-flex flex-column w-100 h-350px justify-content-between'>
                {SMTPdetail()}
              </div>
              <div className='d-flex justify-content-end'>
                <button
                  className='px-7 py-3'
                  style={{
                    border: '1px solid red',
                    borderRadius: '5px',
                    color: '#fff',
                    fontSize: '15px',
                    background: 'red',
                    fontWeight: '700',
                  }}
                  data-bs-dismiss={
                    !errors.Host && !errors.Service && !errors.Password && !errors.Mail
                      ? 'modal'
                      : ''
                  }
                  onClick={() => {
                    updateSMTP()
                  }}
                >
                  Cập nhật
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
})
export default EditSMTP
