import axios, {AxiosResponse} from 'axios'
import {useEffect, useRef, useState} from 'react'
import {URL_BOOKING_STUDIO} from '../../../../../setup/URL'
import EditSMTP from './Components/EditSMTP'
import './SettingSMTP.scss'
type Props = {}
export interface SMTP {
  id: number
  Host: string
  Service: string
  Mail: string
  Password: string
  createdAt: string
  updatedAt: string
}
const SMTPSetting = (props: Props) => {
  const [flag, setFlag] = useState(false)
  const [success, setSuccess] = useState(false)
  const [SMTP, setSMTP] = useState<SMTP[]>([
    {
      id: 0,
      Host: '',
      Service: '',
      Mail: '',
      Password: '',
      createdAt: '',
      updatedAt: '',
    },
  ])
  const timeOutRef = useRef<any>(null)
  const classtd = 'border border border-secondary border-2'
  useEffect(() => {
    const getSMTP = () => {
      let promise = axios({
        url: `${URL_BOOKING_STUDIO}smtp`,
        method: 'GET',
      })
      promise.then((result: AxiosResponse<any>) => {
        console.log('Thanh cong')
        //Nếu gọi api thành công
        // => set lại state
        setSMTP(result.data)
        setFlag(false)
        setSuccess(false)
      })
      promise.catch((err) => {
        console.log('That bai')
        console.log(err.response.data)
      })
    }
    getSMTP()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [flag])
  const SMTPItem = () => {
    return SMTP.map((itm, index) => {
      return (
        <tr key={index} className='pagination-tr align-middle'>
          <td className={classtd}>{itm.id}</td>
          <td className={classtd}>{itm.Host}</td>
          <td className={classtd}>{itm.Service}</td>
          <td className={classtd}> {itm.Mail}</td>
          <td className={classtd}>{itm.Password}</td>
          <td className={classtd}>{itm.createdAt}</td>
          <td className={classtd}>{itm.updatedAt}</td>
          <td className={classtd}>
            <EditSMTP
              id={itm.id}
              flag={(e: any) => {
                setSuccess(e)
                if (timeOutRef.current) {
                  clearTimeout(timeOutRef.current)
                }
                timeOutRef.current = setTimeout(() => {
                  setFlag(e)
                }, 2000)
              }}
            />
          </td>
        </tr>
      )
    })
  }
  return (
    <div className='d-flex flex-column w-75 h-100 position-relative'>
      <div className='updateSMTP-container' style={{transform: 'translateY(-30px)'}}>
        {success ? (
          <div className='updateSMTP-success'>
            Cập nhật thành công <i className='fa-solid fa-check updateSMTP-success'></i>
          </div>
        ) : (
          <></>
        )}
      </div>
      <table className='table border border border-secondary border-2'>
        <thead className='border border border-secondary border-2 fs-5 fw-bolder table_header'>
          <tr className=' align-middle pagination-tr'>
            <th scope='col'>id</th>
            <th scope='col'>Host</th>
            <th scope='col'>Service</th>
            <th scope='col'>Mail</th>
            <th scope='col'>Password</th>
            <th scope='col'>Ngày tạo</th>
            <th scope='col'>Ngày cập nhật</th>
            <th scope='col' style={{width: '160px'}}>
              Chỉnh sửa
            </th>
          </tr>
        </thead>
        <tbody>{SMTPItem()}</tbody>
      </table>
    </div>
  )
}

export default SMTPSetting
